import {Card} from 'antd';
import React, {useState} from 'react';
import {useTranslation} from 'react-i18next';
import {useSearchParams} from 'react-router-dom';
import styled from 'styled-components';
import LoginForm from './LoginForm';

const extractValueFromEventAndThen = (next: any) => (event: any) => {
    next(event.target.value);
};

const Background = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to right bottom, #2185d0, #009ad7, #00aabf, #00b58a, #21ba45);
    background-size: 200% 100%;
    animation: Gradient 15s ease infinite;
    z-index: -1;

    @keyframes Gradient {
        0% {
            background-position: 0% 0%;
        }

        50% {
            background-position: 100% 0%;
        }

        100% {
            background-position: 0% 0%;
        }
    }
`;

const Wrapper = styled.div`
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 10%;
`;

const LoginBlock = styled(Card)`
    width: 30rem;
    max-width: 450px;
    box-sizing: border-box;
`;

const Login = (): JSX.Element => {
    const [searchParams] = useSearchParams();
    const {t} = useTranslation();
    const [isLoading, setIsLoading] = useState(false);
    const [loginError, setLoginError] = useState('');
    const authUrl = process.env.REACT_APP_AUTH_URL || '';
    const [message, setMessage] = useState<string>();

    const redirectTo = searchParams.get('dest') ?? '/';

    const _processLogin = async (login: string, password: string) => {
        try {
            setIsLoading(true);
            setLoginError('');

            const response = await fetch(authUrl, {
                method: 'POST',
                headers: new Headers([['Content-Type', 'application/json']]),
                body: JSON.stringify({
                    login,
                    password
                })
            });

            if (response.status === 401) {
                throw new Error('error.bad_credentials');
            }

            if (!response.ok) {
                throw new Error('error.no_server_response');
            }

            window.location.replace(redirectTo);
        } catch (err) {
            let msg = err.message;
            if (err.message.indexOf('NetworkError') > -1) {
                msg = 'error.no_server_response';
            }
            setLoginError(t(msg));
        } finally {
            setIsLoading(false);
        }
    };

    return <LoginForm onSubmit={_processLogin} loading={isLoading} loginError={loginError} />;
};
export default Login;
